@import url('https://fonts.googleapis.com/css2?family=Roboto:wght@400;700;900&display=swap');
*{
    font-family: 'Roboto','sans-serif';
    font-size: 18px;
}
body{
    display: flex;
    justify-content: center;
    align-items: center;
}

.container{
    
    left: 50%;
    top: 50%;
    position: absolute;
    transform:translate(-50%, -50%);
    margin:top,left;
    width: 256px;
    text-align: center;
    border: 10px solid black;
    background:black ;
    border-radius: 13px;
    border: 10px solid rgb(148, 29, 228); 
}
.keypad{
    display: grid;
    grid-template-columns: repeat(4,1fr);
    grid-auto-rows: minmax(60px,auto);
    background-color: rgb(65, 56, 56);
}

input[type="text"]{
    height: 75px;
    width: 250px;
    background-color: black;
    border: 0px;
    color: #ffffff;
    text-align: right;
    font-weight: 500;
    letter-spacing: 1px;
}

#clear{
    grid-column: 1/3;
    grid-row: 1;
    color: #262824;

}

#result{
    grid-column: 3/5;
    grid-row: 5;

}

