input:focus,
input:active,
button:focus,
button:active{
    outline: none;
}
.highlight{
    background: rgb(6, 194, 184);
    margin: 5px;
    border: none;
    border-radius: 5px;
    cursor: pointer;
    font-weight: 500;
    

}